<template>
  <div class="home">
    <div ref="elem" id="render-target" style="margin: 0 auto;"></div>
  </div>
</template>

<script>
import * as Matter from 'matter-js'

export default {
  mounted() {
    let ex = this.makeWorld();
    console.log(ex);
  },
  methods: {
    makeWorld() {
      let Engine = Matter.Engine,
          Render = Matter.Render,
          Runner = Matter.Runner,
          Composites = Matter.Composites,
          MouseConstraint = Matter.MouseConstraint,
          Mouse = Matter.Mouse,
          World = Matter.World,
          Bodies = Matter.Bodies;

      // create engine
      let engine = Engine.create();
      let world = engine.world;

      // create renderer
      let render = Render.create({
        element: this.$refs.elem, // document.getElementById('render-target'),
        engine: engine,
        options: {
          width: 800,
          height: 300,
          showAngleIndicator: true,
          showCollisions: true
        }
      });

      Render.run(render);

      // create runner
      var runner = Runner.create();
      Runner.run(runner, engine);

      // World.add(world, [
      //     // walls
      //     Bodies.rectangle(400, 0, 800, 50, { isStatic: true }),
      //     Bodies.rectangle(400, 600, 800, 50, { isStatic: true }),
      //     Bodies.rectangle(800, 300, 50, 600, { isStatic: true }),
      //     Bodies.rectangle(0, 300, 50, 600, { isStatic: true })
      // ]);

      // var scale = 0.9;
      // World.add(world, Composites.car(150, 100, 150 * scale, 30 * scale, 30 * scale));

      let scale = 0.8;
      World.add(world, Composites.car(350, 300, 150 * scale, 30 * scale, 30 * scale));

      var ropeA = Composites.stack(100, 50, 8, 1, 10, 10, function(x, y) {
          return Bodies.rectangle(x, y, 50, 20 );
      });
      Composites.chain(ropeA, 0.5, 0, -0.5, 0, { stiffness: 0.8, length: 2, render: { type: 'line' } });
      // Composites.add(ropeA, Constraint.create({
      //     bodyB: ropeA.bodies[0],
      //     pointB: { x: -25, y: 0 },
      //     pointA: { x: ropeA.bodies[0].position.x, y: ropeA.bodies[0].position.y },
      //     stiffness: 0.5
      // }));
      World.add(world, [ ropeA ]);

      World.add(world, [
        Bodies.rectangle(200, 150, 400, 20, { isStatic: true, angle: Math.PI * 0.06 }),
        Bodies.rectangle(500, 350, 650, 20, { isStatic: true, angle: -Math.PI * 0.06 }),
        Bodies.rectangle(300, 560, 600, 20, { isStatic: true, angle: Math.PI * 0.04 })
      ]);

      // add mouse control
      var mouse = Mouse.create(render.canvas),
        mouseConstraint = MouseConstraint.create(engine, {
          mouse: mouse,
          constraint: {
            stiffness: 0.2,
            render: {
              visible: false
            }
          }
        });

      World.add(world, mouseConstraint);

      // keep the mouse in sync with rendering
      render.mouse = mouse;

      // fit the render viewport to the scene
      Render.lookAt(render, {
        min: { x: 0, y: 0 },
        max: { x: 800, y: 600 }
      });

      // context for MatterTools.Demo
      return {
        engine: engine,
        runner: runner,
        render: render,
        canvas: render.canvas,
        stop: function() {
          Matter.Render.stop(render);
          Matter.Runner.stop(runner);
        }
      };
    },
  },
}
</script>
